input[type="reset"].large,
input[type="button"].large,
button.large,
.button.large {
  font-size: 0.7rem;
  height: 3.2625rem;
  line-height: 3.2625rem;
  padding: 0 3.25rem;
}

input[type="submit"].wide,
input[type="reset"].wide,
input[type="button"].wide,
button.wide,
.button.wide {
  min-width: 13rem;
}

