}

#wrapper > section > header h1:after,
#wrapper > section > header h2:after {
  background: #43d9b8;
  border-radius: 0.5rem;
  content: "";
  display: block;
  height: 0.5rem;
  position: absolute;
  top: 0.75rem;
  width: 0.5rem;
