  #wrapper > section > header > * {
    max-width: 25rem;
  }

  #wrapper > section > header > :last-child {
    margin-bottom: 0;
  }

  #wrapper > section > header:before {
    display: none;
  }

  #wrapper > section > header h1:before,
  #wrapper > section > header h2:before {
    display: none;
  }

  #wrapper > section > header h1:after,
  #wrapper > section > header h2:after {
    display: none;
  }

  #wrapper > section > .content > :last-child {
    margin-bottom: 0;
  }

  #wrapper > section > .content > section {
    left: 0;
  }

  #wrapper > section > .content > section:first-child {
    margin-top: 0;
  }

  #wrapper > section > .content > section > header {
    width: 100%;
  }

  #wrapper > section > .content > section:last-child {
    margin-bottom: 0;
  }

  #wrapper > section > footer > :last-child {
    margin-bottom: 0;
  }

  #wrapper > section.intro {
    margin-bottom: 0;
  }

  #wrapper > section.intro > header {
    margin-bottom: 0;
  }

  #wrapper > section.intro > header ul.actions {
    display: none;
  }

  #wrapper > section.intro > header > * {
    margin-left: 0;
    margin-right: 0;
    width: 100%;
  }

  #wrapper > section.intro > header > ul.actions {
    margin-left: -1rem;
  }

  #wrapper > section.intro > header:before {
    margin-left: 0;
  }

  #wrapper > section:last-of-type > header:after {
    display: none;
  }

  #wrapper .copyright {
    left: 0;
    width: 100%;
  }

  #wrapper section {
    margin: 6rem 0 0 0;
  }

  #wrapper > section {
    margin: 0;
  }

  #wrapper > section > header {
    padding: 4rem 4rem;
  }

  #wrapper > section > .content {
    padding: 4rem 4rem;
  }

  #wrapper > section > .content > section {
    margin: 4rem 0;
  }

  #wrapper > section > footer {
    padding: 0 4rem 4rem 4rem;
  }

  #wrapper > section.intro > header {
    padding: 8rem 4rem 5rem 4rem;
  }

  #wrapper .copyright {
    padding: 0 4rem 4rem 4rem;
  }
}

@media screen and (max-width: 736px) {
  #wrapper section {
    margin: 3rem 0 0 0;
  }

  #wrapper > section {
    margin: 0;
  }

  #wrapper > section > header {
    padding: 3rem 2rem;
  }

  #wrapper > section > .content {
    padding: 5rem 3.5rem;
  }

  #wrapper > section > .content > section {
    margin: 3rem 0;
  }

  #wrapper > section > footer {
    padding: 0 2rem 3rem 2rem;
  }

  #wrapper > section.intro > header {
    padding: 5.5rem 2rem 2.5rem 2rem;
  }

  #wrapper .copyright {
    padding: 0 2rem 2rem 2rem;
  }
}

@media screen and (max-width: 360px) {
  #wrapper section {
    margin: 2.25rem 0 0 0;
  }

  #wrapper > section {
    margin: 0;
  }

  #wrapper > section > header {
    padding: 2.25rem 1.5rem;
  }

  #wrapper > section > .content {
    padding: 3.25rem 2.5rem;
  }

  #wrapper > section > .content > section {
    margin: 2.25rem 0;
  }

  #wrapper > section > footer {
    padding: 0 1.5rem 2.25rem 1.5rem;
  }

  #wrapper > section.intro > header {
    padding: 4.875rem 1.5rem 1.875rem 1.5rem;
  }

  #wrapper .copyright {
    padding: 0 1.5rem 1.5rem 1.5rem;
  }
}

body.is-mobile #wrapper:before {
  background-attachment: scroll;
  background-position: 50% -3%;
  background-repeat: repeat-y;
  background-size: 150% auto;
}

body.is-mobile #wrapper > section > header {
  background-attachment: fixed;
  background-position: 25% 50%;
}

body.is-mobile #wrapper > section.intro > header {
  background-position: 25% 23%;
}

#wrapper:before {
  transition: opacity 1.3s ease;
}

body.is-preload #wrapper:before {
  opacity: 0;
}

#wrapper > section.intro {
  transition: opacity 1.3s ease;
  opacity: 1;
}

#wrapper > section.intro > header {
  transition: transform 1s ease;
}

#wrapper > section.intro > .content {
  transition: transform 1s ease;
}

body.is-preload #wrapper > section.intro {
  opacity: 0;
}

body.is-preload #wrapper > section.intro > header {
  transform: translateY(1rem);
}

body.is-preload #wrapper > section.intro > .content {
  transform: translateY(-1rem);
}

@media screen and (max-width: 1280px) {
  #wrapper > section.intro > header > * {
    transition: transform 1s ease, opacity 1.3s ease;
  }

  body.is-preload #wrapper > section.intro > header {
    transform: none;
  }

  body.is-preload #wrapper > section.intro > header > * {
    transform: translate(-0.5rem);
    opacity: 0;
  }

  body.is-preload #wrapper > section.intro > .content {
    transform: none;
  }
}

body.is-ie #wrapper section {
  display: flex;
  flex-wrap: wrap;
}

body.is-ie #wrapper section > header {
  width: 50%;
}

body.is-ie #wrapper section > .content {
  width: 50%;
}

body.is-ie #wrapper section > footer {
  width: 50%;
  text-align: left;
  margin-left: 50%;
  padding: 5rem;
}

body.is-ie #wrapper > section > .content > section {
  width: 100vw;
}

@media screen and (max-width: 1152px) {
  /* mobile css */

  .content {
    background: #fff;
  }

  .Projects h3 {
    font-size: 1.2em;
  }

  .Projects h3 a {
    border: 0;
  }

  #wrapper > section.intro > .content {
    background: transparent !important;
  }
  .image.fill:before {
    content: "";
    display: block;
    position: fixed;
    left: 0;
    top: -9.5%;
    width: 100%;
    height: 124.4vh;
    z-index: -10;
    background: url(/images/pic01.jpg) no-repeat center center;
    background-size: cover;
    overflow: hidden;
  }

  .items:after {
    content: "";
    display: block;
    position: fixed;
    left: 0;
    bottom: -30.3%;
    width: 100%;
    height: 200px;
    /* z-index: -10; */
    background-color: #000;
    /* background: url(/images/pic01.jpg) no-repeat center center; */
    /* background-size: cover; */
    /* overflow: hidden; */
    z-index: 1;
  }

  .hidemobile {
    display: none;
  }
  .gallery a.landscape img {
    padding: 0.5em;
  }
  .dashblink {
    font-size: 100%;
    font-weight: 200;
    -webkit-animation: blinkingText 1.4s infinite;
            animation: blinkingText 1.4s infinite;
    text-decoration: blink;
  }

  #wrapper .copyright {
    background: #fff !important;
    /* margin-bottom: -6px; */
    /* margin-top: -1px; */
  }
}

@media screen and (min-width: 1153px) {
  /* desktop css */

  .image.fill {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    background: url(/images/pic01.jpg);
    background-position: 106%;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: 57%;
  }

  body.is-ie #wrapper section > footer {
    padding: 4rem;
  }

  .hidedesktop {
    display: none;
  }
}

/* Global CSS Rules */

* {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently*/
}

.intro a {
  font-family: "Raleway", Helvetica, sans-serif;
  font-size: 0.8rem;
  letter-spacing: 0.175em;
  line-height: 2.5;
  text-transform: uppercase;
}

.intro p {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid #43d9b7; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.15em; /* Adjust as needed */
  animation: blinktext 5s steps(60, end), blink-caret 0.75s step-end infinite;
  -webkit-animation: blinktext 5.6s steps(60, end),
    blink-caret 0.75s step-end infinite;
  margin-right: 0px;
  /* animation-delay: 0.08s !important; */
  padding-right: 2.5px;
  max-width: 17.61rem !important;
}

@-webkit-keyframes blinktext {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blinktext {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #43d9b7;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #43d9b7;
  }
}

